import React from 'react'

function capitalize(str) {
  if (!str || typeof str !== 'string') {
    return ''
  }
  return str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();
}

const textStyle = {
  fontSize: 16,
  fontFamily: 'Roboto, Verdana, Arial'
}

function norm(str) {
  return typeof str === 'string' && str.toLowerCase().replace(/[]/gi, 'e') || ''
}

const HighLight = ({ text, query }) => {
  if (!text) {
    return null
  }
  const placeholderStyle = {
    pointerEvents: 'none',
    position: 'relative',
    top: -21,
    left: 3,
    color: "silver",
    ...textStyle
  }
  const queryStyle = { color: 'black' }
  const pos = text && norm(text).indexOf(norm(query))
  let before = null;
  let after = query && text && (query.length < text.length) && text.slice(query.length) || null;
  if (pos && pos > -1) {
    before = text.slice(0, pos);
    after = text.slice(pos + query.length);
  }
  return (
   <div style={ placeholderStyle }>
        { before }
        <span style={queryStyle}>{ query }</span>
        { after }
   </div>
  )
}

class FuzzyInput extends React.Component {
  constructor(...args) {
    super(...args)
    this.state = {
      query: '',
      resultIndex: 0
    }
  }
  onInput(e) {
    this.setState({
      query: e.target.value
    });
  }
  onKeyDown(e) {
    if (this.state.query && e.keyCode === 9 || e.keyCode === 13) {
      e.preventDefault();
      e.stopPropagation();
      this.setState(prevState => ({
        query: this.getResult(prevState),
        resultIndex: 0
      }), () => {
        // props.onSelect
      });
    } else if (this.state.query && e.keyCode === 38) {
      e.preventDefault();
      e.stopPropagation();
      this.setState(prevState => ({
        resultIndex: Math.max(0, prevState.resultIndex - 1)
      }), () => {

      });
    } else if (this.state.query && e.keyCode === 40) {
      e.preventDefault();
      e.stopPropagation();
      this.setState(prevState => ({
        resultIndex: Math.min(this.getResults(prevState.query).length - 1, prevState.resultIndex + 1)
      }), () => {

      });
    }
  }
  getResults() {
    return this.props.getItems(this.state.query) || [];
  }
  getResult(state) {
    const results = this.getResults();
    if (results.length && this.state.resultIndex < results.length) {
      return results[this.state.resultIndex]
    }
  }
  render() {
    const result = this.getResult(this.state);
    let query = this.state.query;
    if (query.length === 1) {
      query = query.toUpperCase();
    }
    const isPerfectMatch = result && (result.length === query.length);
    const inputStyle = {
      color: (result && !isPerfectMatch && 'white' || 'black'),
      ...textStyle
    }
    const divStyle = {
      position: 'relative'
    }

    return (
      <div style={ divStyle }>
        <input
          placeholder={ this.props.placeholder }
          onKeyDown={this.onKeyDown.bind(this)}
          type="text"
          style={inputStyle}
          value={query}
          onChange={this.onInput.bind(this)}
         />
        {!isPerfectMatch && <HighLight text={ result } query={ query }/>}
      </div>
     )
  }
}

export default FuzzyInput



